@media (max-width: 1100px) {





}


@media (max-width: 767px) {

  .main_page_container{
    min-height: 80vh;
    padding: 20px 10px ;
    background-color: whitesmoke;
  }
  .detail_page_container{
    padding: 20px 20px ;
  
  }
  .cards_container{
    padding: 20px 10px ;
    background-color: whitesmoke;
  
  }
  .info_container{
    padding: 10px;

  }
  .map_container{
    margin-top: 10px;
  }
  .alert_container {
    position:sticky;
    top: 11vh;
    right: 0px;
    width: 100vw;
    padding: 10px 15px;
    border-radius: 0px 0px 0px 0px;
  }

  .phone_legend_container{
    width: 100%;
    display: block;
    background-color: rgb(255, 255, 255);
  
  }
  .desktop_legend_container{
    display: none;
  }
  .legend_item{
    font-size: 12px;
  }
  .layercontrol_item label{
    font-size: 12px;
    width: 100%;
  }


  .contact-us-form {
    padding: 0px 0px;
  }
  .footer_container{
    width: 100%;
    padding: 10px 10px;
  }
  

  .footer_counter {
    width: 100%;
    margin: 10px 0px;
  }

  .footer_counter p {
    font-size: 14px;
  }

  .copyright_container p {
    font-size: 12px;
    margin: 0;
    text-align: center;

  }


  .banner-content h1 {
    font-size: 44px;
  }

  .banner-content p {
    font-size: 18px;
  }

  .home_page_btn {
    padding: 10px 20px;
    font-size: 16px;
  }

  .footer_container h4 {
    font-size: 14px;

  }




  .footer_menu {
    padding: 10px 5px;
    text-align: center;

  }

  .footer_menu a {
    text-decoration: none;
    padding: 5px;
    color: black;
    font-size: 14px;

  }




}


