  display: none;
  position: absolute;
  top: 10px;
  right: 10px;
  border:1px solid rgb(228, 228, 228);
  border-radius: 5px;
  padding: 10px;
  z-index: 1000000;
  background-color: white;
}
.layercontrol_item label{
  width: 100%;
  align-items: center;
  display: flex;
  font-size: 14px;
}
.layercontrol_item input{
  margin-right: 2px;
}
.layercontrol_container.visible {
  display: block;
}




.alert_container {
  position:fixed;
  top: 12vh;
  right: 0px;
  min-width: 20vw;
  /* color: rgb(0, 43, 63);
  background-color: #6ed5f7; */
  color: rgb(0, 0, 0);
  background-color: #FFC107;
  padding: 15px 25px;
  border-radius: 5px 0px 0px 5px;
  opacity: 1;
  visibility: visible;
  animation: fade-in 0.5s ease-out forwards;
  z-index: 1000000000;
}

.alert_container.hide-alert {
  animation: fade-out 0.5s ease-in forwards;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    /* transform: translateX(50px); */
  }
}







