
.navbar_container{
  background-color: #FFF;
  position:fixed;
  width: 100vw;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 1000000000;
  align-items: center;
  box-shadow: 4px 2px 5px rgba(0, 0, 0, 0.1);

}

.navbar_main {
  display: flex;
  align-items: center;
  z-index: 1000000000;
  background-color: #FFF;
  /* border-bottom: 2px dashed rgb(196, 196, 196); */
}

.nav-container {
  display: flex;
  margin: auto 5vw;
  align-items: center;
  width: 100%;
  height: 10vh;
  justify-content: space-between;

}

.nav_logo img {
  width: 150px;
}


.nav-menu {
  display: flex;
  margin: 0;
  list-style: none;
  text-align: center;
  margin-right: 2rem;

}

.nav-links {
  color: #000000;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
  margin: 0px;
  list-style: none;
}

.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 5px;
  list-style: none;
  margin: 0px;

}

