.nav-item:hover{


}
.nav-item .active {
  color: #234677;
  border: 1px solid #154677;
  font-weight: bold;
  border-radius: 20px;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {

  .nav-menu {
    display: flex;
    padding: 0;
    margin: 0;
    flex-direction: column;
    width: 100%;
    /* width: 100vw; */
    border-top: 1px solid #fff;
    position: absolute;
    top: 10.5vh;
    left: -110%;
    height: 90vh;
    opacity: 1;
    transition: all 0.5s ease;

  }

  .nav-menu.active {
    background: white;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 10000000;
    margin: 0px;
    width: 100%;
  }

  .nav-item .active {
    color: #234677;
    border: none;
    font-weight: bold;
  }

  .nav-links {
    padding: 10px;
    width: 100%;
    display: table;
    font-size: 20px;
    margin: 0px;
    list-style: none;
    /* text-align: left; */
  }

  .nav-icon {
    display: block;
    font-size: 1.8rem;
    cursor: pointer;

  }
}